import React from 'react';
import { Box } from '@mui/material';

const FormControlWrapper = ({ children, marginLeft, marginRight }) => {
  return (
    <Box mt={3} sx={{ width: '100%' }} ml={marginLeft ? marginLeft : '0'} mr={marginRight ? marginRight : '0'}>
      {children}
    </Box>
  );
};

export default FormControlWrapper;
