import { Box, Typography, Button, Modal } from '@mui/material';
import { history } from 'src/utils/history';

const style = {
  modal: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    p: 4,
    borderRadius: '6px',
  }
}

const Alert = ({ open, setOpen, message, error, redirect }) => {
  function handleClose() {
    setOpen(false)
    if (redirect) {
      history.push(redirect);
    }
  }
  return (
    <Modal
      open={open}
      // onClose={() => setOpen(false)}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style.modal}>
        <Typography id="modal-modal-title" variant="h6" component="h2" textAlign={'center'}>
          NOTIFICATION
        </Typography>
        <Box sx={{display: 'flex', alignItems: 'center', justifyContent: 'center'}} mb={5} mt={2}>
          {
            error ? '' : <img src={require('src/assets/images/success.jpg')} alt="success.jpg" style={{marginRight: '8px', width: '30px', height: '30px' }} />
          }
          <Typography id="modal-modal-description" > {message ? message : 'Submitted successfully'} </Typography>
        </Box>
        <Box sx={{display: 'flex', alignItems: 'center', justifyContent: 'center'}} >
          <Button variant="contained" sx={{ width: '50%' }} type="submit" onClick={() => handleClose()}>
            Continue
          </Button>
        </Box>
      </Box>
    </Modal>
  );
};

export default Alert;
