import React, { useState } from 'react';
import { Grid, TextField, Typography, Box, Link, InputAdornment, IconButton, Checkbox, FormControlLabel, Select, MenuItem, InputBase, Card } from '@mui/material';
import LoadingButton from '@mui/lab/LoadingButton';
import { VisibilityOutlined, VisibilityOffOutlined } from '@mui/icons-material';
import FormControlWrapper from 'src/components/FormControlWrapper';
import { styled, css } from '@mui/material/styles';
import { Formik } from 'formik';
import DatePicker from '@mui/lab/DatePicker';
import * as Yup from 'yup';
import { useDispatch } from 'src/store';
import { Redirect, useLocation, Link as RouterLink } from 'react-router-dom';
import FormLabel from 'src/components/FormLabel';
import useAuth from 'src/hooks/useAuth';
import Page from 'src/components/Page';
// import _ from 'lodash';
import countries from 'src/assets/json/country.json';
import { register } from 'src/store/slices/auth';
import countryPhones from 'src/assets/json/countryPhone.json';
import Alert from 'src/components/Alert'

const RegisterBox = styled(Card)(({theme}) => ({
    width: '100%',
    maxWidth: 1024,
    margin: '3rem 0',
    padding: '4rem 4rem',
    borderRadius: '0.5rem'
}))
const style = {
  fontWeightBold: {
    fontWeight: 'bold'
  },
  textRed: {
    color: 'red'
  },
  selectPadding: {
    padding: '0',
    margin: '0'
  }
};

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  'label + &': {
    marginTop: theme.spacing(3)
  },
  '& .MuiInputBase-input': {
    marginTop: '6px',
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '8px 26px 8px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    }
  }
}));

const LoginView = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const [dob, setDob] = React.useState(null);
  const [country, setCountry] = React.useState('');
  const [agree, setAgree] = React.useState(false);
  const [countryPhone, setCountryPhone] = React.useState('');
  const { isAuthenticated } = useAuth();
  const [showPassword, setShowPassword] = useState(false);
  const [showRePassword, setShowRePassword] = useState(false);
  const [open, setOpen] = useState(false);
  const [alertError, setAlertError] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');

  function convert(str) {
    var date = new Date(str),
      mnth = ("0" + (date.getMonth() + 1)).slice(-2),
      day = ("0" + date.getDate()).slice(-2);
    return [day, mnth, date.getFullYear()].join("/");
  }

  if (isAuthenticated !== null && isAuthenticated === true) {
    const system = location.pathname.split('/')[1];
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');
    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}`} />;
  }

  return (
    <Page title="Register New Account">
      <Grid container sx={{ height: '100vh' }}>
        <Grid item xs={12} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <RegisterBox>
            <Formik
              initialValues={{
                address: '',
                admin: false,
                // country: '',
                // dob: '',
                // phone_country_code: '',
                email: '',
                first_name: '',
                last_name: '',
                phone: '',
                state: '',
                username: '',
                password: '',
                repeatpassword: ''
              }}
              validationSchema={Yup.object().shape({
                username: Yup.string().required('User name is required'),
                email: Yup.string().required('Email is required'),
                first_name: Yup.string().required('First name is required'),
                last_name: Yup.string().required('Last name is required'),
                password: Yup.string().required('Password is required'),
                repeatpassword: Yup.string().required('Repeat password is required')
              })}
              onSubmit={async (values, { setSubmitting }) => {
                try {
                  const params = {
                    ...values,
                    dob: convert(dob),
                    country: country,
                    phone_country_code: countryPhone
                  }
                  if (values.password !== values.repeatpassword) {
                    setOpen(true)
                    setAlertError(true)
                    setAlertMessage('Repeat password not match with the password')
                    return false
                  }
                  dispatch(register(params)).then(() => {
                    setSubmitting(false);
                  });
                } catch (err) {
                  setAlertError(true)
                  setAlertMessage('Something wrong, please try again later!')
                  setOpen(true)
                }
              }}
            >
              {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
                <form onSubmit={handleSubmit}>
                  <Typography variant="h5" component="div" style={style.fontWeightBold} align="center" marginBottom="32px">
                    Register New Account
                  </Typography>
                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="User name" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="username"
                        placeholder="Username"
                        error={Boolean(touched.username && errors.username)}
                        helperText={touched.username && errors.username}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.username}
                      />
                    </FormControlWrapper>
                    <FormControlWrapper marginLeft={5}>
                      <FormLabel label="Email" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="email"
                        placeholder="Email"
                        error={Boolean(touched.email && errors.email)}
                        helperText={touched.email && errors.email}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.email}
                      />
                    </FormControlWrapper>
                  </Box>

                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="First name" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="first_name"
                        placeholder="First name"
                        error={Boolean(touched.first_name && errors.first_name)}
                        helperText={touched.first_name && errors.first_name}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.first_name}
                      />
                    </FormControlWrapper>
                    <FormControlWrapper marginLeft={5}>
                      <FormLabel label="Last name" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="last_name"
                        placeholder="Last name"
                        error={Boolean(touched.last_name && errors.last_name)}
                        helperText={touched.last_name && errors.last_name}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.last_name}
                      />
                    </FormControlWrapper>
                  </Box>

                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="Date of birth" />
                      <DatePicker
                        value={dob}
                        onChange={(newDob) => {
                          setDob(newDob);
                        }}
                        renderInput={(params) => <TextField fullWidth size="small" {...params} />}
                      />
                      {/* <TextField fullWidth size="small" margin="dense" name="dob" placeholder="Date of birth" onBlur={handleBlur} onChange={handleChange} value={values.dob} /> */}
                    </FormControlWrapper>

                    <FormControlWrapper marginLeft={5}>
                      <FormLabel label="Phone number" />
                      <Box sx={{ display: 'flex' }}>
                        <Box sx={{ width: '80px' }} mr={2}>
                          <Select
                            value={countryPhone}
                            onChange={(event) => {
                              setCountryPhone(event.target.value);
                            }}
                            fullWidth
                            input={<BootstrapInput />}
                          >
                            {countryPhones
                              ? countryPhones.map((x) => {
                                  return <MenuItem value={x.code} key={x.code}>{x.code}</MenuItem>;
                                })
                              : ''}
                          </Select>
                        </Box>
                        <TextField fullWidth size="small" margin="dense" name="phone" placeholder="Phone number" onBlur={handleBlur} onChange={handleChange} value={values.phone} />
                      </Box>
                    </FormControlWrapper>
                  </Box>
                  <Box>
                    <FormControlWrapper>
                      <FormLabel label="Address" />
                      <TextField fullWidth size="small" margin="dense" name="address" placeholder="Address" onBlur={handleBlur} onChange={handleChange} value={values.address} />
                    </FormControlWrapper>
                  </Box>
                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="State" />
                      <TextField fullWidth size="small" margin="dense" name="state" placeholder="State" onBlur={handleBlur} onChange={handleChange} value={values.state} />
                    </FormControlWrapper>
                    <FormControlWrapper marginLeft={5}>
                      <FormLabel label="Country" />
                      <Select
                        value={country}
                        onChange={(event) => {
                          setCountry(event.target.value);
                        }}
                        fullWidth
                        input={<BootstrapInput />}
                      >
                        {countries
                          ? countries.map((x) => {
                              return <MenuItem value={x.value} key={x.value}>{x.value}</MenuItem>;
                            })
                          : ''}
                      </Select>
                    </FormControlWrapper>
                  </Box>
                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="Password" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="password"
                        placeholder="Password"
                        error={Boolean(touched.password && errors.password)}
                        helperText={touched.password && errors.password}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.password}
                        type={showPassword ? 'text' : 'password'}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton onClick={() => setShowPassword((showPassword) => !showPassword)}>
                                {showPassword ? <VisibilityOffOutlined /> : <VisibilityOutlined />}
                              </IconButton>
                            </InputAdornment>
                          )
                        }}
                      />
                    </FormControlWrapper>
                    <FormControlWrapper marginLeft={5}>
                      <FormLabel label="Repeat password" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="repeatpassword"
                        placeholder="Repeat password"
                        error={Boolean(touched.repeatpassword && errors.repeatpassword)}
                        helperText={touched.repeatpassword && errors.repeatpassword}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.repeatpassword}
                        type={showRePassword ? 'text' : 'password'}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton onClick={() => setShowRePassword((showRePassword) => !showRePassword)}>
                                {showRePassword ? <VisibilityOffOutlined /> : <VisibilityOutlined />}
                              </IconButton>
                            </InputAdornment>
                          )
                        }}
                      />
                    </FormControlWrapper>
                  </Box>
                  <Box mt={2} display="flex" justifyContent="space-between">
                    <FormControlLabel
                      checked={agree}
                      value="end"
                      control={<Checkbox />}
                      label="I agree to all the Term, Privacy Policy"
                      labelPlacement="end"
                      onChange={() => setAgree(!agree)}
                    ></FormControlLabel>
                  </Box>

                  <Box mt={4} textAlign="center">
                    <LoadingButton variant="contained" sx={{ width: '50%' }} type="submit" loading={isSubmitting} disabled={!agree}>
                      Create account
                    </LoadingButton>
                  </Box>

                  <Box mt={1.5} textAlign="center">
                    <Typography component="span">Already an account? </Typography>
                    <Link component={RouterLink} to="/vleppo/login">Login</Link>
                  </Box>
                </form>
              )}
            </Formik>
          </RegisterBox>
        </Grid>
      </Grid>
      <Alert open={open} setOpen={ () => {setOpen(false)}} message={alertMessage} error={alertError} />
    </Page>
  );
};

export default LoginView;
