import { Helmet } from 'react-helmet';
import PropTypes from 'prop-types';

const Page = ({ title = '', children, ...rest }) => {
  return (
    <div {...rest}>
      <Helmet>
        <title>{title}</title>
      </Helmet>
      {children}
    </div>
  );
};

Page.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string
};

export default Page;
