import React from 'react';
import { Typography } from '@mui/material';

const FormLabel = ({ label, required, marginBottom }) => {
  return (
    <Typography marginBottom={marginBottom !== undefined ? marginBottom : 1} fontWeight={500}>
      {label}&nbsp;
      {required && (
        <Typography component="span" sx={{ color: 'red' }}>
          *
        </Typography>
      )}
    </Typography>
  );
};

export default FormLabel;
